___CSS_LOADER_EXPORT___.push([module.id, ".w-8{width:2rem}.h-8{height:2rem}.text-sm{font-size:.875rem;line-height:1.25rem}.text-xs{font-size:.75rem;line-height:1rem}.space-y-6{margin-top:1.5rem;margin-bottom:1.5rem}", "",{"version":3,"sources":["webpack://./resources/js/components/profile/LogoutOtherBrowserSessions.vue"],"names":[],"mappings":"AAyLA,KACA,UACA,CACA,KACA,WACA,CACA,SACA,iBAAA,CACA,mBACA,CAEA,SACA,gBAAA,CACA,gBACA,CACA,WACA,iBAAA,CACA,oBACA","sourcesContent":["<template>\n  <v-card elevation=\"3\">\n    <v-container>\n      <v-card-title class=\"text-center\">\n        جلسات المتصفح || سجلات الدخول\n      </v-card-title>\n      <v-row align-center justify-center>\n        <v-col cols=\"12\" md=\"4\">\n          <v-card-subtitle>\n            إدارة جلساتك النشطة وتسجيل الخروج منها على المتصفحات والأجهزة\n            الأخرى.\n          </v-card-subtitle>\n        </v-col>\n        <v-col cols=\"12\" md=\"8\">\n          <v-card-text>\n            <p>\n              إذا لزم الأمر ، يمكنك تسجيل الخروج من جميع جلسات المتصفح الأخرى\n              عبر جميع أجهزتك. بعض جلساتك الأخيرة مذكورة أدناه ؛ ومع ذلك ، قد لا\n              تكون هذه القائمة شاملة. إذا شعرت أنه تم اختراق حسابك ، فيجب عليك\n              أيضًا تحديث كلمة المرور الخاصة بك.\n            </p>\n            <!-- Other Browser Sessions -->\n            <div class=\"mt-5 space-y-6\" v-if=\"sessions.length > 0\">\n              <div\n                class=\"d-flex flex-wrap align-center\"\n                v-for=\"(session, i) in sessions\"\n                :key=\"i\"\n              >\n                <div>\n                  <svg\n                    fill=\"none\"\n                    stroke-linecap=\"round\"\n                    stroke-linejoin=\"round\"\n                    stroke-width=\"2\"\n                    viewBox=\"0 0 24 24\"\n                    stroke=\"currentColor\"\n                    class=\"w-8 h-8 gray--text\"\n                    v-if=\"session.agent.is_desktop\"\n                  >\n                    <path\n                      d=\"M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z\"\n                    ></path>\n                  </svg>\n\n                  <svg\n                    xmlns=\"http://www.w3.org/2000/svg\"\n                    viewBox=\"0 0 24 24\"\n                    stroke-width=\"2\"\n                    stroke=\"currentColor\"\n                    fill=\"none\"\n                    stroke-linecap=\"round\"\n                    stroke-linejoin=\"round\"\n                    class=\"w-8 h-8 gray--text\"\n                    v-else\n                  >\n                    <path d=\"M0 0h24v24H0z\" stroke=\"none\"></path>\n                    <rect x=\"7\" y=\"4\" width=\"10\" height=\"16\" rx=\"1\"></rect>\n                    <path d=\"M11 5h2M12 17v.01\"></path>\n                  </svg>\n                </div>\n\n                <div class=\"mr-3\">\n                  <div class=\"text-sm text--gray\">\n                    {{ session.agent.platform }} - {{ session.agent.browser }}\n                  </div>\n\n                  <div>\n                    <div class=\"text-xs text--gray\">\n                      {{ session.ip_address }},\n\n                      <span\n                        class=\"green--text font-semibold\"\n                        v-if=\"session.is_current_device\"\n                        >This device</span\n                      >\n                      <span v-else>Last active {{ session.last_active }}</span>\n                    </div>\n                  </div>\n                </div>\n              </div>\n            </div>\n          </v-card-text>\n        </v-col>\n        <v-col>\n          <v-card-actions>\n            <div class=\"flex items-center mt-5\">\n              <v-btn @click.native=\"confirmLogout\" color=\"primary white--text\">\n                تسجيل الخروج من جلسات المتصفح الأخرى\n              </v-btn>\n            </div>\n          </v-card-actions>\n        </v-col>\n      </v-row>\n    </v-container>\n\n    <!-- Logout Other Devices Confirmation Modal -->\n    <v-dialog v-model=\"confirmingLogout\" persistent max-width=\"500\">\n      <v-card>\n        <v-card-title class=\"headline\">\n          تسجيل الخروج من جلسات المتصفح الأخرى\n        </v-card-title>\n        <v-card-text>\n          <p>\n            يرجى إدخال كلمة المرور الخاصة بك لتأكيد رغبتك في تسجيل الخروج من\n            جلسات المتصفح الأخرى عبر جميع أجهزتك.\n          </p>\n\n          <c-password\n            name=\"password\"\n            label=\"كلمة المرور\"\n            v-model=\"form.password\"\n            ref=\"password\"\n            @keyup.enter.native=\"logoutOtherBrowserSessions\"\n          />\n        </v-card-text>\n        <v-card-actions>\n          <v-spacer></v-spacer>\n          <v-btn\n            color=\"primary white--text\"\n            dark\n            @click=\"confirmingLogout = false\"\n          >\n            لا\n          </v-btn>\n          <v-btn\n            color=\"primary white--text\"\n            dark\n            @click=\"logoutOtherBrowserSessions\"\n            :loading=\"loading\"\n          >\n            تسجيل الخروج من جلسات المتصفح الأخرى\n          </v-btn>\n        </v-card-actions>\n      </v-card>\n    </v-dialog>\n  </v-card>\n</template>\n\n<script>\nexport default {\n  props: [\"sessions\"],\n  data() {\n    return {\n      dialog: false,\n      loading: false,\n      confirmingLogout: false,\n\n      form: {\n        _method: \"DELETE\",\n        password: \"\",\n      },\n    };\n  },\n\n  methods: {\n    confirmLogout() {\n      this.form.password = \"\";\n      this.clearErrors();\n      this.confirmingLogout = true;\n    },\n\n    logoutOtherBrowserSessions() {\n      this.loading = true;\n      this.$inertia.post(route(\"other-browser-sessions.destroy\"), this.form, {\n        preserveScroll: true,\n        onProgress: (progress) => {\n          this.confirmingLogout = true;\n          this.loading = true;\n        },\n        onSuccess: (page) => {\n          if (!this.hasErrors(\"logoutOtherBrowserSessions\")) {\n            this.confirmingLogout = false;\n          }\n          this.loading = false;\n        },\n        onFinish: () => {\n          this.loading = false;\n        },\n      });\n    },\n  },\n};\n</script>\n\n<style>\n.w-8 {\n  width: 2rem;\n}\n.h-8 {\n  height: 2rem;\n}\n.text-sm {\n  font-size: 0.875rem;\n  line-height: 1.25rem;\n}\n\n.text-xs {\n  font-size: 0.75rem;\n  line-height: 1rem;\n}\n.space-y-6 {\n  margin-top: 1.5rem;\n  margin-bottom: 1.5rem;\n}\n</style>\n"],"sourceRoot":""}]);
